/* MODO LIGHT */
.contenedor-principal {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #6809e1;
}
/* ============================================== */
/* MODO DARK */
.dark {
  background-color: #373737;
}
/* ============================================== */
/* CLASE PARA DAR TRANSICION AL BG DE ELEMENTOS */
.transition {
  transition: background-color 0.5s ease;
}
